<template>
  <div>
    <input v-model="localValue" type="text" placeholder="Type the word above here">
  </div>
</template>


<style lang="scss" scoped>
div {
  input[type="text"] {
    background-color: $black;
    border: none;
    box-sizing: border-box;
    border-radius: 0;
    color: $text-white-primary;
    display: block;
    font-size: $fsize-h5;
    text-align: center;
    padding: 40px;
    width: 100%;
    -webkit-appearance: none;
    appearance: none;

    &:focus {
      outline: none;
    }
  }
}
</style>

<script>
export default {
  name: 'TheWordInputField',
  props: {
    value: {
      type: String
    }
  },
  computed: {
    localValue: {
      get(){
        return this.value
      },
      set(value){
        this.$emit('input', value)
      }
    }
  }
}
</script>